.contact{
    width: 100%;
    margin: 4rem auto;
    display: flex;
    justify-content: space-between;
    padding: 0 5rem;
    grid-gap: 2rem;
    &__box1{
        padding: 0;
        &__contacts{
            list-style: none;
            padding: 0;
            li{
                padding: .7rem 0;
                display: flex;
                align-items: center;
                font-size: 1rem;
                
                a{
                    color: #125082;
                    grid-gap: .5rem;
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    font-size: 1rem;
                    font-weight: bold;
                }
                svg{
                    width: 34px;
                    height: 34px;
                    color: #125082;
                }
            }
        }
        &__media{
            list-style: none;
            padding: 0;
            display: flex;
            grid-gap: .5rem;
            li{
                svg{
                    width: 30px;
                    height: 30px;
                    color: #125082;
                }
            }
        }

        &__map{
            padding: 0 0rem;
            h2{
                font-size: 2rem;
                margin-top: 2rem;
                color: #125082;
            }

            &__mapouter{
                border-radius: 8px;
                
            }
        }
    }

    &__box2{
        width: 100%;
        margin: 0 auto;
        &__form{
            background-color: #f1f1f1;
            width: 80%;
            float: right;
            padding: 1rem;

            &__container{
                width: 100%;
                margin: 0 auto;
                &__div{
                    padding: 1rem;
                    &__label{
                        display: block;
                        font-size:1rem;
                        color: #125082; 
                        font-weight: 600;             
                    }
    
                    &__input{
                        width: 100%;
                        padding: .875rem 1rem;
                        border-radius: 8px;
                        border: 1px solid #125082;
                    }

                    &__buttonSubmit{
                        width: 100%;
                        height: 47px;
                        border-radius: 11px;
                        background-color: #125082;
                        border: none;
                        font-size: 1rem;
                        color: #fff;
                        text-transform: uppercase;
                        letter-spacing: 2px;
                        margin: 0 auto;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        font-weight: 600;
                        box-shadow: 1px 1px 2px 1px #0f426b;

                    }
                }
                &__textarea{
                    width: 100%;
                    margin: 0 auto;
                    border-radius: 8px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 100px;
                    padding: .875rem 1rem;
                    border: 1px solid #125082;
                }
            }
            
        }
    }

}

@media (max-width: 768px){
    .contact{
        width: 100%;
        padding: 0;
        margin-top: 0rem;
        flex-direction: column;
        &__box1{

            &__contacts{
                padding: 1rem;
                width: 100%;
            }
            width: 100%;
            &__map{
                padding: 1rem;
                margin-top: -4rem;
                h2{
                    font-size: 1.5rem;
                    margin-top: 3rem;
                    color: #125082;
                }
                &__mapouter{
                    padding: 1rem;
                    width: 100%;
                    height: auto;
                }
            }

            &__media{
                margin-top: -2rem;
                padding: 1.2rem;
            }
        }

        &__box2{
            width: 100%;
            padding:  0;
            &__form{
                float: none;
                padding: 0;
                margin: 1rem auto;
            }
        }
    }
}