.formWebZoho{
    display: block;
    background-color: #d4d4d4;
    width: 90%;
    padding: 2rem;
    border-radius: 8px;
    margin-left: 4rem;
    &__input{

        &__select{
            width: 100%;
            padding: .875rem;
            border: 1px solid #125082;
            border-radius: 8px;
            background-color: #ffffff;
            :focus{
                outline: none;
            }
        }

        input{
            width: 100%;
            padding: .875rem;
            border: 1px solid #125082;
            border-radius: 8px;
            :focus{
                outline: none;
            }
            &__select{
                width: 100%;
            padding: .875rem;
            border: 1px solid #125082;
            border-radius: 8px;
            :focus{
                outline: none;
            }
            }
        }   
        label{
            display: block;
            margin-top: .7rem;
            font-size: 1rem;
            font-weight: 500;
        }

        textarea{
            resize: none;
            width: 100%;
            border-radius: 8px;
            height: 100px;
        }
    }
}